import { Sprite } from 'cc';
import { UIOpacity } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { UITransform } from 'cc';
import { v3 } from 'cc';
import { Label } from 'cc';
import { SpriteFrame } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { PandaGameData } from '../model/PandaGameData';
import { Tween } from 'cc';
import { ParticleSystem } from 'cc';
import PandaGameRes from '../model/PandaGameRes';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

const GameData = PandaGameData.data;

@ccclass('PandaTipsPop')
export class PandaTipsPop extends Component {
    @property(SpriteFrame)
    tipsPopSFGroup: SpriteFrame[] = [];
    @property(Label)
    scoreTxt: Label;
    @property(Label)
    tipTxt: Label;

    @property(ParticleSystem)
    effGroup: ParticleSystem[] = [];

    playIndex = 0;

    start() {

    }
    resetBg() {
        this.node.getComponent(Sprite).spriteFrame = this.tipsPopSFGroup[0];
        this.effGroup.forEach(v => {
            v.node.active = false;
        })
    }
    setBg(type: 2 | 3 | 4) {
        this.node.getComponent(Sprite).spriteFrame = this.tipsPopSFGroup[type - 1];
        this.effGroup[type - 2].node.active = false;
        this.effGroup[type - 2].node.active = true;
        this.effGroup[type - 2].stop();
        this.effGroup[type - 2].play();
        this.effGroup[type - 2].node.children.forEach(v => {
            v.active = false;
            v.active = true;
            let eff = v.getComponent(ParticleSystem);
            eff.stop();
            eff.play();
        })
    }
    playTipsWinPop(num: number) {
        if (!num) return;
        let mul = num / GameData.gameData.betScore;
        this.tipTxt.node.active = false;
        this.scoreTxt.node.parent.active = true;
        let tipsPopGroup = this.scoreTxt.node.parent;
        let winTxt = this.scoreTxt.node;
        let winNode = this.scoreTxt.node.parent.getChildByName("winNode");

        let tipsPopUIO = tipsPopGroup.getComponent(UIOpacity);
        tipsPopGroup.scale = v3(1.2, 1.2, 1.2);
        this.scoreTxt.string = Num.exchangeToThousands(1, num);
        this.scheduleOnce(() => {
            winNode.position = v3(-winTxt.getComponent(UITransform).width / 2);
            winTxt.position = v3(winNode.position.x + winNode.getComponent(UITransform).width * winNode.scale.x / 2 + 10, 5);
            if (mul >= 10 && mul <= 20) {
                this.setBg(2);
            } else if (mul > 20 && mul <= 100) {
                this.setBg(3);
            } else if (mul > 100) {
                this.setBg(4);
            }
            AudioMgr.inst.playOneShot(PandaGameRes.res.tipsPopWin, 1, ModuleDef.SLOTS_PANDA);
            tween(tipsPopGroup).to(1, { scale: Vec3.ONE }, { easing: "bounceOut" }).start();
            tween(tipsPopUIO).to(0.05, { opacity: 255 }).start();
        }, 0.001);
    }
    hideTipsPop() {
        let tipsPopGroup = this.scoreTxt.node.parent;
        let tipsPopUIO = tipsPopGroup.getComponent(UIOpacity);
        let tipUIO = this.tipTxt.node.getComponent(UIOpacity);

        tipsPopUIO.opacity = 255;
        // this.tipTxt.string = I18n.ins().t("slotPanda_tip" + (this.playIndex + 1));
        tween(tipsPopUIO).to(0.05, { opacity: 0 }).call(() => {
            this.scoreTxt.node.parent.active = false;
            this.resetBg();
            this.tipTxt.node.active = true;
            tipUIO.opacity = 255;
        }).start();
    }
    playTipsTxt() {
        if (this.scoreTxt.node.parent.active) return;
        this.tipTxt.node.active = true;
        // this.tipTxt.string = I18n.ins().t("slotPanda_tip" + (this.playIndex + 1));
        let tipUIO = this.tipTxt.node.getComponent(UIOpacity);
        tipUIO.opacity = 0;
        let timer = () => {
            Tween.stopAllByTarget(this.tipTxt.node);
            Tween.stopAllByTarget(this.tipTxt.node.getComponent(UIOpacity));
            tween(tipUIO).to(0.25, { opacity: 255 }).start();
            this.tipTxt.node.position =
                v3(this.tipTxt.node.getComponent(UITransform).width > this.tipTxt.node.parent.getComponent(UITransform).width ?
                    -this.tipTxt.node.getComponent(UITransform).width / 2 +
                    (this.tipTxt.node.getComponent(UITransform).width - this.tipTxt.node.parent.getComponent(UITransform).width) / 2
                    : -this.tipTxt.node.getComponent(UITransform).width / 2
                    , 3.579);
            this.playIndex == 2 && tween(this.tipTxt.node).delay(3).to(7, { position: v3(-1779.265, 3.579) }).start();
            tween(tipUIO).to(0.25, { opacity: 255 }).start();
            tween(tipUIO).delay(9.5).to(0.25, { opacity: 0 }).start();
            this.playIndex = this.playIndex >= 2 ? 0 : this.playIndex + 1;
        }
        this.scheduleOnce(timer, 0.001);
    }
}


