import { ApiReturn } from "tsrpc-browser";
import { SlotPandaGameScene, SlotPandaStartGameResp } from "./PandaInterface";
import { ResPlaySlots } from "db://assets/module_basic/shared/protocols/public/slots/PtlPlaySlots";
import { Num } from "db://assets/core_tgx/framework/util/Num";
import { Vec3 } from "cc";
import { ParticleSystem, Node } from "cc";
import { CurveRange } from "cc";

interface Data {
    sceneData: SlotPandaGameScene;
    gameData: SlotPandaStartGameResp;
    autoArr: number[];
    autoCountIndex: number;
    leftAutoCount: number;
    reduceLabel: number;
    addLabel: number;
    winLabel: number;
}
const _data: Data = {
    sceneData: null,
    gameData: null,
    autoArr: [10, 30, 50, 100, 1000],
    autoCountIndex: 0,
    leftAutoCount: 0,
    reduceLabel: 0,
    addLabel: 0,
    winLabel: 0
}
const cacheName = {
    betScore: '_betScore'
}

const ElementTable = {
    "wild": 201,
    "shoots": 101,
    "plants": 102,
    "box": 103,
    "ring": 104,
    "bamboo": 105,
    "panda": 106,
}

const LineCount = {
    1: [0, 0, 0],
    2: [1, 1, 1],
    3: [2, 2, 2],
    4: [3, 2, 3],
    5: [0, 1, 0],
    6: [1, 2, 1],
    7: [2, 3, 2],
    8: [3, 3, 3],
    9: [1, 0, 1],
    10: [2, 1, 2],
}

export class PandaGameData {
    static data = _data;
    static cacheName = cacheName;

    /**
    * 递归旋转、缩放子节点下粒子组件的节点
    * @param root 要查找的节点
    * @param componentType 组件类型
    */
    static setNodesByComponent(root: Node, angle: number = 90, scale: Vec3 = new Vec3(0.75, 0.75, 0.75)) {
        // 检查当前节点是否包含指定类型的组件
        const comp = root.getComponent(ParticleSystem);
        if (comp) {
            // const deg = angle * (Math.PI / 180);
            // if (comp.startRotationZ.mode === CurveRange.Mode.Constant) {
            //     comp.startRotationZ.constant = deg;
            // } else if (comp.startRotationZ.mode === CurveRange.Mode.TwoConstants) {
            //     comp.startRotationZ.constantMin += deg;
            //     comp.startRotationZ.constantMax += deg;
            // }
            comp.node.scale = scale.clone().multiply(comp.node.scale.clone());
        }

        root.children.forEach(child => this.setNodesByComponent(child, angle, scale));
    }

    /**
    * 返回的数据转换
    * @param data 
    * @param bet 下注额
    */
    static conver(data: ApiReturn<ResPlaySlots>, bet: number) {
        const result: SlotPandaStartGameResp = {
            betScore: 0,
            code: 200,
            main: { colums: [], hitType: 0, isAllSame: 0, lines: [], winScore: 0, wheelIndex: [] },
            mini: {
                winScore: 0,
                list: [],
                elementId: 0,
                multiple: 0
            }
        };
        if (data.isSucc) {
            result.code = 200;
            result.betScore = bet;
            // result.winScore = Num.toThousandsBack(data.res.payout);
            const res = data.res.results[0];

            //连线游戏数据
            for (let i = 0; i < 3; i++) {
                //初始化连线游戏的数据
                result.main.colums.push({ elementId: [] });
                for (let j = 0; j < 4; j++) {
                    result.main.colums[i].elementId.push(0);
                }
            }
            //所有的元素符号
            for (let i = res.symbols.length - 1; i >= 0; i--) {
                if (res.symbols[i].id === 'null') continue; //空位置，跳过
                result.main.colums[res.symbols[i].coordinate.reel - 1].elementId[4 - res.symbols[i].coordinate.row] = ElementTable[res.symbols[i].id];
            }
            if (res.paylines.length > 0) {
                result.main.hitType = 1; //线路中奖
                //所有的中奖线
                for (let i = 0; i < res.paylines.length; i++) {
                    const payline = res.paylines[i];
                    const lineNo = parseInt(payline.id); //线号
                    const lineScore = Num.toThousandsBack(payline.payout); //线得分
                    result.main.lines.push({ lineNo: lineNo, lineScore: lineScore, line: LineCount[lineNo] });
                }
            }
            if (!res.bigEvents) {
                result.mini = null;
            } else {
                //幸运游戏数据
                result.mini.list.push(result.main);
                result.main = null;
                result.mini.multiple = res.roulette ? res.roulette.multiple : 0; //倍数
                result.mini.winScore = Num.toThousandsBack(res.payout);
            }
        } else {
            result.code = 500;
        }

        return result;
    }
}   