import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { PandaGameData } from '../model/PandaGameData';
import { sp } from 'cc';
import { Input } from 'cc';
import PandaGameEvent from '../model/PandaGameEvent';
import { ParticleSystem } from 'cc';
import { tween } from 'cc';
import { UIOpacity } from 'cc';
import { director } from 'cc';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import PandaGameRes from '../model/PandaGameRes';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
const { ccclass, property } = _decorator;

const GameData = PandaGameData.data;

@ccclass('PandaBigWinPop')
export class PandaBigWinPop extends Component {
    @property(Label)
    winScoreTxt: Label;
    @property(sp.Skeleton)
    pandaAni: sp.Skeleton;
    @property(sp.SkeletonData)
    aniDataGroup: sp.SkeletonData[] = [];
    @property(ParticleSystem)
    bigWinEffGroup: ParticleSystem[] = [];
    @property(sp.Skeleton)
    goldAni: sp.Skeleton;

    isClick = false;
    isClickHide = false;
    start() {
        let canHide = false;
        PandaGameData.setNodesByComponent(this.node);
        this.node.on(Input.EventType.TOUCH_START, () => {
            let winScore = GameData.gameData.mini ? GameData.gameData.mini.winScore : GameData.gameData.main.winScore;
            if (this.isClick && canHide && !this.isClickHide) {
                this.isClickHide = true;
                director.emit(PandaGameEvent.event.bigWinPopHide, 0);
            } else if (!this.isClick) {
                canHide = false;
                this.pandaAni.paused = true;
                // root.audio.stop(PandaGameRes.res.bigWinRollNumber);
                director.emit(PandaGameEvent.event.stopAudio);
                this.scheduleOnce(() => {
                    let mul = winScore / GameData.gameData.betScore;
                    let index: 0 | 1 | 2 = 0;
                    if (mul >= 100) {
                        index = 2;
                    } else if (mul >= 20) {
                        index = 1;
                    } else {
                        index = 0;
                    }
                    this.pandaAni.skeletonData = this.aniDataGroup[index];
                    this.playAni(index, true);


                    director.emit(PandaGameEvent.event.bigWinPopHide, 3);


                    this.isClick = true;
                }, 0.001);

                this.winScoreTxt.string = Num.exchangeToThousands(1, winScore);
                let timer = () => {
                    canHide = true;
                }
                this.scheduleOnce(timer, 0.7);
            }
        });
    }
    playAni(type: 0 | 1 | 2, isClick: boolean = false) {
        if (this.isClick) return;
        this.goldAni.node.active = true;

        !isClick && AudioMgr.inst.playOneShot(PandaGameRes.res['bigWin' + (type + 1)], 1, ModuleDef.SLOTS_PANDA);

        if (this && this.node) this.node.active = true;
        this.pandaAni.paused = false;
        this.pandaAni.skeletonData = this.aniDataGroup[type];
        this.bigWinEffGroup.forEach(v => {
            if (v && v.node) v.node.active = false;
        })

        if (this.bigWinEffGroup[type] && this.bigWinEffGroup[type].node) this.bigWinEffGroup[type].node.active = true;
        this.bigWinEffGroup[type].stop();
        this.bigWinEffGroup[type].play();
        this.scheduleOnce(() => {
            if (!type && !this.isClick) {
                this.goldAni.setAnimation(0, "animation", false);
            }
            if (this.pandaAni && this.pandaAni.node) this.pandaAni.node.active = true;
            this.pandaAni.setAnimation(0, "animation", true);
        }, 0.001);
    }
    hide() {
        this.scheduleOnce(() => {
            let bigWinPopUIO = this.node.getComponent(UIOpacity);
            // root.audio.stop(PandaGameRes.res.bigWinRollNumber);
            director.emit(PandaGameEvent.event.stopAudio);
            this.goldAni.node.active = false;

            bigWinPopUIO.opacity = 255;
            tween(bigWinPopUIO).to(0.2, { opacity: 0 }).call(() => {
                this.pandaAni.paused = true;
                if (!this.node.active) return;

                this.node.active = false;
                this.bigWinEffGroup.forEach(v => {
                    if (v && v.node) v.node.active = false;
                })
            }).start();
        }, 0.01);
    }
    resetData() {
        this.pandaAni.skeletonData = this.aniDataGroup[0];
        this.isClick = false;
    }
}