import { _decorator, Component, Node } from 'cc';
import { PandaGameLogic } from '../contorller/PandaGameLogic';
import { Label } from 'cc';
import { PandaGameData } from '../model/PandaGameData';
import { tween } from 'cc';
import { Button } from 'cc';
import { UIOpacity } from 'cc';
import { v3 } from 'cc';
import { Tween } from 'cc';
import { Sprite } from 'cc';
import { Prefab } from 'cc';
import { PandaAuto } from '../component/PandaAuto';
import { PandaTipsPop } from '../component/PandaTipsPop';
import { PandaBigWinPop } from '../component/PandaBigWinPop';
import { sp } from 'cc';
import { PandaAnimation } from '../contorller/PandaAnimation';
import { ParticleSystem } from 'cc';
import { ParticleSystem2D } from 'cc';
import { PandaGameMiniGame } from './PandaGameMiniGame';
import { BaseSlot, SlotEaseType, SlotStopType } from 'db://assets/module_basic/ui_slot/BaseSlot';
import { Num } from 'db://assets/core_tgx/framework/util/Num';
import { UserMgr } from 'db://assets/module_basic/scripts/UserMgr';
import { RoomMgr } from 'db://assets/module_basic/scripts/RoomMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
import PandaGameRes from '../model/PandaGameRes';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { UIHelp, UIhelpParam } from 'db://assets/module_basic/ui_help/UIHelp';
const { ccclass, property } = _decorator;

const GameData = PandaGameData.data;

@ccclass('PandaGameMainView')
export class PandaGameMainView extends Component {
    @property(BaseSlot)
    centerRollGroup: BaseSlot;
    @property(Label)
    betTxt: Label;
    @property(Label)
    winTxt: Label;
    @property(Label)
    balanceTxt: Label;

    @property(Button)
    btnAdd: Button;
    @property(Button)
    btnSub: Button;
    @property(Button)
    btnPlay: Button;
    @property(Button)
    btnAuto: Button;

    @property(Button)
    btnSpeedCut: Button;
    @property(Button)
    btnSpeedUp: Button;
    @property(Node)
    rotatePlay: Node;
    @property(Sprite)
    btnPlayGrayGroup: Sprite[] = [];
    @property(Node)
    clickIconPop: Node;
    @property(Node)
    clickIconMask: Node;
    @property(Node)
    iconResistMask: Node;
    @property(Node)
    lineGroup: Node;
    @property(Node)
    lineNumbersGroup: Node;
    @property(Node)
    winLineGroup: Node;
    @property(Prefab)
    winIcon: Prefab;
    @property(Node)
    bgGroup: Node;
    @property(Node)
    winIconGroup: Node;
    @property(Node)
    baseIconGroup: Node;
    @property(Node)
    missWildIconGroup: Node;
    @property(Node)
    autoPop: Node;
    @property(Button)
    btnStop: Button;
    @property(Label)
    autoCountLab: Label;
    @property(PandaTipsPop)
    tipsPop: PandaTipsPop;
    @property(PandaBigWinPop)
    bigWinPop: PandaBigWinPop;
    @property(PandaAnimation)
    panda: PandaAnimation;
    @property(Node)
    miniGameTitle: Node;
    @property([sp.SkeletonData])
    anis: sp.SkeletonData[] = [];
    @property(sp.Skeleton)
    miniGameCoin: sp.Skeleton;
    @property(ParticleSystem2D)
    WildTuowei: ParticleSystem2D;
    @property(ParticleSystem)
    touchPlayEff: ParticleSystem;
    @property(ParticleSystem)
    touchPlayLoopEff: ParticleSystem;
    @property(ParticleSystem)
    miniCoinEffGroup: ParticleSystem[] = [];
    @property(Node)
    iconWinBgEffNode: Node;
    @property(ParticleSystem)
    bambooEffGroup: ParticleSystem[] = [];
    @property(Node)
    miniMask: Node;
    @property(Node)
    bottomNode: Node;
    @property(Node)
    settingClose: Node;
    @property(Node)
    miniGameGuangzhu: Node;
    @property(Node)
    allSameImg: Node;
    @property(Node)
    btnFirstClick: Node;
    @property(Node)
    btnFastClick: Node;
    @property(sp.Skeleton)
    baseAllSameCoinAni: sp.Skeleton;
    @property(ParticleSystem)
    baseAllSameEff: ParticleSystem;
    @property(Node)
    baseAllSameImg: Node;
    @property(Node)
    videoClose: Node;
    @property(Node)
    videoOpen: Node;
    @property(Node)
    openningMask: Node;
    @property(Button)
    btnBetAdd: Button;
    @property(Button)
    btnBetSub: Button;
    @property(Node)
    miniGamePop: Node;
    @property(Node)
    enterMiniGamePop: Node;
    @property(PandaGameMiniGame)
    miniGame: PandaGameMiniGame;
    @property({ type: Node, tooltip: '放置所有二级界面' })
    panelParentNode: Node = null;

    private _mainLogic: PandaGameLogic;
    private isFirstTouchSpeedUp = false;
    private isOpenVideo = false;
    public static ins: PandaGameMainView;
    start() {
        PandaGameMainView.ins = this;
        this._mainLogic = this.node.getComponent(PandaGameLogic);
        this.setBtnAlpha(this.btnSub);
        this.tweenRotateStand();

        this.openningMask.active = false;
        this.centerRollGroup.easingIn = SlotEaseType.Back;
        this.centerRollGroup.easingOut = SlotEaseType.Elastic;

        const currentTime = localStorage.getItem("Panda_currentTime");
        if (!currentTime || currentTime === '') {
            this.isOpenVideo = true;
        } else {
            // if (currentTime === tool.date.formatDate()) {
            //     this.isOpenVideo = false;
            // } else {
            //     this.isOpenVideo = true;
            // }
        }
        this.scheduleOnce(() => {
            if (this.isOpenVideo) {

            } else {
                this.openningMask.active = false;
            }
            this.miniGamePop.active = false;
        }, 0.001);

        this.videoClose.active = !this.isOpenVideo;
        this.videoOpen.active = this.isOpenVideo;
    }
    //视频时间
    private onVideoEvent() {
        console.log("视频播放完成");
        if (this.isOpenVideo) {
            console.log("关闭视频按钮");
            this.isOpenVideo = false;
            // localStorage.setItem("Panda_currentTime", tool.date.formatDate());
            this.videoClose.active = !this.isOpenVideo;
            this.videoOpen.active = this.isOpenVideo;
            this.openningMask.active = false;
        }
    }
    init() {

    }
    onTouchPlay() {
        this._mainLogic.touchPlay();

        this.touchPlayEff.node.active = true;
        this.touchPlayEff.stop();
        this.touchPlayEff.play();

        this.touchPlayLoopEff.node.active = true;
        this.touchPlayLoopEff.stop();
        this.touchPlayLoopEff.play();
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    recoverSpeed() {
        this.btnFirstClick.active = true;
        this.btnFastClick.active = true;
        this.centerRollGroup.easingIn = SlotEaseType.Back;
        this.centerRollGroup.easingOut = SlotEaseType.Elastic;

        this.centerRollGroup.stopType = SlotStopType.立即陆续;
        this._mainLogic.isFast = false;
        this._mainLogic.timeScale = 1;
    }
    tweenRotatePlay() {
        Tween.stopAllByTarget(this.rotatePlay);
        let rotate = this.rotatePlay.eulerAngles.z;
        tween(this.rotatePlay).tag(11).to(0.5, { eulerAngles: v3(0, 0, rotate - 360) }).call(() => {
            this.tweenRotatePlay();
        }).start();
    }
    tweenRotateStand() {
        Tween.stopAllByTarget(this.rotatePlay);
        let rotate = this.rotatePlay.eulerAngles.z;
        tween(this.rotatePlay).tag(12).to(8, { eulerAngles: v3(0, 0, rotate - 360) }).call(() => {
            this.tweenRotateStand();
        }).start();
    }
    onGameOver() {
        this.setFastClick(false);
        this.btnSub.interactable = this._mainLogic.betIndex > 0;
        this.btnAdd.interactable = this._mainLogic.betIndex < this._mainLogic.betLength - 1;
        this.setBtnAlpha(this.btnSub, this._mainLogic.betIndex > 0);
        this.setBtnAlpha(this.btnAdd, this._mainLogic.betIndex < this._mainLogic.betLength - 1);
        this.betTxt.node.parent.getComponent(Button).interactable = true;
        Tween.stopAllByTarget(this.rotatePlay);
        this.setSpriteGrayscale(this.rotatePlay, false);
        this.btnPlayGrayGroup.forEach(v => {
            this.setSpriteGrayscale(v.node, false);
        })
        this.tweenRotateStand();
        this.setBtnAlpha(this.btnAuto, !this._mainLogic.isAuto);
    }
    onTouchSpeedUp() {
        if (this.isFirstTouchSpeedUp) {
            this.btnSpeedUp.node.active = false;
            this.btnSpeedCut.node.active = true;

            this.centerRollGroup.easingIn = SlotEaseType.Back;
            this.centerRollGroup.easingOut = SlotEaseType.Elastic;

            this.centerRollGroup.stopType = SlotStopType.立即陆续;

            this._mainLogic.isFast = false;
            this._mainLogic.timeScale = 1;
        } else {
            this.centerRollGroup.stopType = SlotStopType.立即停止;
            this.centerRollGroup.easingIn = SlotEaseType.Linear;
            this.centerRollGroup.easingOut = SlotEaseType.Linear;

            this._mainLogic.isFast = true;
            this._mainLogic.timeScale = 0.5;
        }
        this.isFirstTouchSpeedUp = true;
        this.btnSpeedUp.node.getChildByName("eff").active = this.isFirstTouchSpeedUp;

        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    onTouchSpeedCut() {
        this.btnSpeedUp.node.getChildByName("eff").active = true;
        this.btnSpeedUp.node.active = true;
        this.btnSpeedCut.node.active = false;
        this.centerRollGroup.stopType = SlotStopType.立即停止;
        this.centerRollGroup.easingIn = SlotEaseType.Linear;
        this.centerRollGroup.easingOut = SlotEaseType.Linear;

        this._mainLogic.timeScale = 0.5;
        this._mainLogic.isFast = true;

        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    onTouchFastClick() {
        this.btnFastClick.active = false;
        this.centerRollGroup.stopType = SlotStopType.立即停止;
        this.centerRollGroup.easingIn = SlotEaseType.Linear;
        this.centerRollGroup.easingOut = SlotEaseType.Linear;

        this._mainLogic.timeScale = 0.5;
        this._mainLogic.isFast = true;
    }
    onTouchFirstClick() {
        return;
        this.unschedule(this.recoverSpeed);
        this.btnFirstClick.active = false;
    }
    setFastClick(flag: boolean = false) {
        if (this._mainLogic.isAuto) return;
        this.btnFirstClick.active = flag;
        this.btnFastClick.active = flag;
    }
    onTouchLastMoney() {
        //打开钱包
        this._mainLogic.onLastMoney();
    }
    onTouchBetSub() {
        this._mainLogic.onBetSub();
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    onTouchBetAdd() {
        this._mainLogic.onBetAdd();
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    onTouchBetTxt() {
        this._mainLogic.onBet();
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    onTouchAuto() {
        this._mainLogic.onOpenAuto();
        // this.autoPop.getComponent(PandaAuto).showPanel();
        // this._mainLogic.touchStopAuto();

        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    onTouchSettingOpen() {
        this.settingClose.active = true;
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    onTouchSettingClose() {
        this.settingClose.active = false;
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    private onHelp() {
        // console.log("---帮助---")
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
        tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("rule"));
    }
    private onPayout() {
        // console.log("---帮助---")
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
        tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("payout"));
    }
    private onMusic() {
        // AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
        AudioMgr.inst.switch = !AudioMgr.inst.switch;
    }
    private onMenu() {
        // console.log("---积分明细---")
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
    }
    onTouchAutoStop() {
        this.btnStop.node.active = false;
        this.btnPlay.node.active = true;
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
        this._mainLogic.touchStopAuto();
    }
    onTouchVideo() {
        // if (this.isOpenVideo) {
        //     localStorage.setItem("Panda_currentTime", tool.date.formatDate());
        // } else {
        //     localStorage.removeItem("Panda_currentTime");
        // }
        // this.isOpenVideo = !this.isOpenVideo;
        // this.videoClose.active = !this.isOpenVideo;
        // this.videoOpen.active = this.isOpenVideo;
        // root.game.control.tips.tip(I18n.ins().t(this.isOpenVideo ? "openSplashVideo" : "closeSplashVideo"));
    }
    private onExit() {
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true, '#e9f7a8', '#8ebc35', 'f0ffd3', '13433a').onClick(async (ok: Boolean) => {
            if (ok) {
                await RoomMgr.inst.exitRoom();
            }
        });
    }
    updateBetTxt(num: string | number) {
        const toNum = Number(num);
        this.betTxt.string = Num.exchangeToThousands(1, toNum);
    }
    updateWinTxt(num: string | number) {
        const toNum = Number(num);
        this.winTxt.string = Num.exchangeToThousands(1, toNum);
    }
    updateBalanceTxt() {
        this.balanceTxt.string = Num.exchangeToThousands(1, UserMgr.inst.coin);
    }
    setSpriteGrayscale(node: Node, bool: boolean) {
        node.getComponent(Sprite).grayscale = bool;
    }
    setBtnAlpha(btn: Button, isShow: boolean = false) {
        btn.getComponent(UIOpacity).opacity = isShow ? 255 : 255 / 2;
    }
}