import { _decorator, Component, Node } from 'cc';
import PandaGameEvent from '../model/PandaGameEvent';
import { SlotPandaGameScene } from '../model/PandaInterface';
import { PandaGameData } from '../model/PandaGameData';
import { view } from 'cc';
import { Vec3 } from 'cc';
import { UITransform } from 'cc';
import { director } from 'cc';
const { ccclass, property } = _decorator;

const GameEvent = PandaGameEvent.event;
const GameData = PandaGameData.data;

@ccclass('PandaMainControl')
export class PandaMainControl extends Component {
    private gameMainTran: UITransform = null;
    onLoad() {
        this.gameMainTran = this.node.getComponent(UITransform);
        // this.events.put(GameEvent.scene + root.game.id, (data: SlotPandaGameScene) => {
        //     console.log("sceneData:", data);
        //     GameData.sceneData = data;
        //     emitter.emit(GameEvent.init);
        // });
        //适配
        // const v = view.getVisibleSize();
        // const scaleV = v.height / this.gameMainTran.height;
        // this.node.scale = new Vec3(scaleV, scaleV, 1);


    }

    public static startGame() {
        this.setData({
            betScore: [100, 200, 300, 400, 500, 600, 700, 800, 900], elementOdd: { 101: 3, 102: 5, 103: 8, 104: 10, 105: 25, 106: 100, 201: 250 }, fakeMiniRate: 10, lineCount: 10
            , mainWheel: [{ lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }, { lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }, { lineWheel: [101, 102, 103, 104, 105, 106, 105, 104, 103, 102, 101] }]
            , miniWheel: [{ lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }, { lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }, { lineWheel: [302, 302, 302, 302, 302, 302, 302, 302, 302, 302, 302] }],
            miniMultiple: [1, 2, 3, 4, 5, 6, 7, 8]
        });
    }

    private static setData(res: SlotPandaGameScene) {
        GameData.sceneData = res;
        director.emit(GameEvent.init, true);
    }
}