import { Vec3 } from 'cc';
import { Label } from 'cc';
import { view } from 'cc';
import { easing } from 'cc';
import { Button } from 'cc';
import { UITransform } from 'cc';
import { v3 } from 'cc';
import { tween } from 'cc';
import { math } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { PandaGameData } from '../model/PandaGameData';
import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { sp } from 'cc';
import { UIOpacity } from 'cc';
import PandaGameEvent from '../model/PandaGameEvent';
import PandaGameRes from '../model/PandaGameRes';
import { v2 } from 'cc';
import { Vec2 } from 'cc';
import { director } from 'cc';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { AudioMgr } from 'db://assets/core_tgx/base/AudioMgr';
const { ccclass, property } = _decorator;

const GameData = PandaGameData.data;
const GameEvent = PandaGameEvent.event;

@ccclass('PandaGameMiniGame')
export class PandaGameMiniGame extends Component {
    @property(Node)
    outerRing: Node;
    @property(Node)
    innerRing: Node;
    @property(Node)
    marble: Node;
    @property(Node)
    mulNumGroup: Node;
    @property(Button)
    btnStart: Button;
    @property(Button)
    mainBtnPlay: Button;
    @property(Button)
    btnAuto: Button;

    @property(SpriteFrame)
    iconSFGroup: SpriteFrame[] = [];
    @property(sp.Skeleton)
    pandaAni: sp.Skeleton;
    @property(Node)
    enterMiniGamePop: Node;

    @property(Node)
    innerSpinEffNode: Node;
    @property(Node)
    innerWinEff: Node;
    @property(Node)
    innerWinEffGroup: Node;
    @property(Node)
    outerWinEffGroup: Node;


    rotationSpeed: number = 400;  // 旋转速度（角度/秒）
    centerPoint: Vec3 = new Vec3(0, -50, 0);  // 旋转的轴心坐标（目标点）
    mulWinIndex = 0;

    private radius: number = 450;  // 圆的半径，表示从轴心到节点的距离
    private angle: number = 0;  // 当前角度

    private randomIndex = 0;
    private canRotation = false;
    private timeArr = [1.04, 1.2, 1.36, 1.5, 1.64, 1.78];
    private winNodeWordPos: Vec3;
    private ballTracking = false;
    private _angle = 0;
    private _isStart = false;
    private isOutRingStop = false;
    private isInnerRingStop = true;
    private outRingIndex: number = 0;
    private nowPlayingSound = "";
    private soundGroup: Array<{ uuid, res }> = [];
    private ballBeforePoint = Vec2.ZERO;
    private ballHitCount = 0;
    private _spinEffId = -1;

    init() {
        this.canRotation = false;
        this.testFlag = true;
        this.marble.parent = this.node;
        this.marble.active = false;
        this.ballTracking = false;
        this.btnStart.node.position = this.mainBtnPlay.node.position;
        this.isOutRingStop = false;
        this.isInnerRingStop = true;
        this.btnStart.node.active = false;
        this.btnAuto.node.active = false;
        this.ballHitCount = 0;
        this.creatIcons();
        this.pandaAni.node.active = true;
        this.pandaAni.paused = false;

        this.innerWinEffGroup.children.forEach(v => {
            v.active = false;
        })
        this.outerWinEffGroup.children.forEach(v => {
            v.active = false;
        })
        this.innerWinEff.active = false;
        this.innerSpinEffNode.active = false;
        if (this._spinEffId != -1) (AudioMgr.inst.stopOneShot(this._spinEffId, PandaGameRes.res.spinEff, ModuleDef.SLOTS_PANDA), this._spinEffId = -1);
        this.scheduleOnce(() => {
            this.onTouchStart();
        }, 0.45);
        this.innerRing.angle = 0;
        this.outerRing.angle = 0;
        this.angle = 0;
        this._angle = 0;
        this.marble.position = v3(0, 408.7);
    }
    tweenPonit1() {
        let danzhu = this.marble;
        let pointA = Vec3.ZERO;
        let pointB = Vec3.ZERO;
        this.mulWinIndex = Math.floor(Math.random() * this.mulNumGroup.children.length);
        this.mulNumGroup.children[this.mulWinIndex].getComponent(Label).string = "X" + GameData.gameData.mini.multiple + "";
        let winNode = this.mulNumGroup.children[this.mulWinIndex];
        switch (this.randomIndex) {
            case 0:
                //撞击正下方撞针
                pointA = v3(36.948, -450.75);
                pointB = v3(7.779, -75.723);
                break;
            case 1:
                //撞击左下方撞针
                pointA = v3(-316.217, -250.153);
                pointB = v3(-24.724, -69.889);
                break;
            case 2:
                //左上方撞针
                pointA = v3(-359.502, 113.263);
                pointB = v3(-27.025, -36.913);
                break;
            case 3:
                //正上方撞针
                pointA = v3(-20.605, 326.264);
                pointB = v3(-3.477, -15.177);
                break;
            case 4:
                //右上方撞针
                pointA = v3(310.056, 168.527);
                pointB = v3(27.316, -26.951);
                break;
            case 5:
                //右下方撞针
                pointA = v3(333.124, -204.141);
                pointB = v3(27.929, -50.895);
                break;
        }
        tween(danzhu).to(0.2, { position: pointA }).call(() => {
            this.playBallHit();

            tween(danzhu).to(1, { position: pointB }).call(() => {
                this.ballTracking = true;

                this.playBallHit();
            })
                .delay(0.4)
                .call(() => {
                }).start();
        }).start();
    }
    creatIcons() {
        let iconGroup = this.outerRing.children;
        for (let i = 0; i < iconGroup.length; i++) {
            const v = iconGroup[i].getComponent(Sprite);
            v.spriteFrame = this.iconSFGroup[i];
            if (this.iconSFGroup[i].name == GameData.gameData.mini.elementId + "") {
                this.outRingIndex = i;
            }
        }

        let mulArr = [];
        mulArr = [...GameData.sceneData.miniMultiple, ...GameData.sceneData.miniMultiple];
        for (let k = 0; k < this.mulNumGroup.children.length; k++) {
            const str = this.mulNumGroup.children[k].getComponent(Label);
            let random = Math.floor(Math.random() * mulArr.length);
            const mulNum = mulArr[random];
            str.string = "X" + mulNum + "";
            mulArr.splice(random, 1);
        }
    }
    update(deltaTime: number) {
        if (this.canRotation) {
            // 根据旋转速度计算角度变化
            this.angle -= this.rotationSpeed * deltaTime;

            // 将角度转换为弧度
            const radian = math.toRadian(this.angle);

            // 计算新的坐标
            const newX = this.centerPoint.x + this.radius * Math.cos(radian);
            const newY = this.centerPoint.y + this.radius * Math.sin(radian);

            // 更新节点位置
            this.marble.setPosition(new Vec3(newX, newY, this.centerPoint.z));
        }

        this.ringRoation(this.outerRing, 3.6);
        this.innerRingRotaion();
        if (this.ballTracking) {
            this.ballHitCount++;
            let danzhu = this.marble;
            let danzhuPos = danzhu.position;
            let endPoint = v3(
                this.winNodeWordPos.x - view.getVisibleSize().width / 2 + 10,
                this.winNodeWordPos.y - view.getVisibleSize().height / 2 + this.innerRing.getComponent(UITransform).height / 4 - 130
            );
            this._angle += 0.007;
            let isSpin = false;
            let speed = 5;
            let isHit = this.isPointInCircleAround(danzhuPos.toVec2(), this.centerPoint.toVec2(), 180) &&
                Vec2.distance(danzhuPos.toVec2(), this.ballBeforePoint) <= 5 && !this.isInnerRingStop && this.ballHitCount % 8 == 0;

            this.ballBeforePoint = danzhuPos.clone().toVec2();
            if (isHit) {
                this.playBallHit();
            }
            if (danzhuPos.x > endPoint.x + speed) {
                danzhu.position = v3(danzhu.position.x - speed + Math.cos(this._angle), danzhuPos.y, danzhuPos.z);
                isSpin = true;
            }
            if (danzhuPos.x < endPoint.x - speed) {
                danzhu.position = v3(danzhu.position.x + speed + Math.cos(this._angle), danzhuPos.y, danzhuPos.z);
                isSpin = true;
            }
            if (danzhuPos.y > endPoint.y + speed) {
                danzhu.position = v3(danzhuPos.x, danzhu.position.y - speed + Math.sin(this._angle), danzhuPos.z);
                isSpin = true;
            }
            if (danzhuPos.y < endPoint.y - speed) {
                danzhu.position = v3(danzhuPos.x, danzhu.position.y + speed + Math.sin(this._angle), danzhuPos.z);
                isSpin = true;
            }
            if (!isSpin) {

            }
        }
    }
    onTouchStart() {
        console.log("进了几次");
        this.testFlag = false;
        this.isInnerRingStop = false;
        this.innerSpinEffNode.active = true;
        AudioMgr.inst.playOneShot(PandaGameRes.res.spinEff, 1, ModuleDef.SLOTS_PANDA).then(aeid => this._spinEffId = aeid);
        this.btnStart.node.active = false;
        this.soundGroup.length = 0;

        this._isStart = true;
        this.marble.active = true;
        this.canRotation = true;
        AudioMgr.inst.playOneShot(PandaGameRes.res.dropBall, 1, ModuleDef.SLOTS_PANDA);


        // root.audio.playPatch(PandaGameRes.res.ballSpin, 1, true, (uuid: string) => {
        //     this.soundGroup.push({ uuid: uuid, res: PandaGameRes.res.ballSpin });
        // });
        // root.audio.playPatch(PandaGameRes.res.xiaoQuan, 0.4, true, (uuid: string) => {
        //     this.soundGroup.push({ uuid: uuid, res: PandaGameRes.res.xiaoQuan });
        // });
        // root.audio.playPatch(PandaGameRes.res.daQuan, 0.4, true, (uuid: string) => {
        //     this.soundGroup.push({ uuid: uuid, res: PandaGameRes.res.daQuan });
        // });

        this.randomIndex = Math.floor(Math.random() * this.timeArr.length);
        this.scheduleOnce(() => {
            this.canRotation = false;
            this.tweenPonit1();
        }, this.timeArr[this.randomIndex]);
        this.scheduleOnce(() => {
            this.testFlag = true;
            this._isStart = false;
        }, 7);
        this.scheduleOnce(() => {
            this.isOutRingStop = true;
        }, 5);
    }
    stopCorona() {
        this.isOutRingStop = true;
    }
    testFlag = true;
    ringRoation(target: Node, rotation: number) {
        if (this.testFlag) return;
    }
    innerRingRotaion() {
        this.winNodeWordPos = this.mulNumGroup.children[this.mulWinIndex].getWorldPosition();
        let outWordPos = this.outerRing.children[this.outRingIndex].getWorldPosition();
        !this.isInnerRingStop && (this.innerRing.angle -= 1.8);
        let angle = this.getAngleBetweenVectors(this.winNodeWordPos, outWordPos) * 180 / Math.PI;

        let _distance = Vec3.distance(this.winNodeWordPos, outWordPos);
        if (angle < 30 && _distance < 135 && this.isOutRingStop && !this.isInnerRingStop) {
            this.isInnerRingStop = true;

            // root.audio.stopPatch(this.stopSound(PandaGameRes.res.ballSpin));
            // root.audio.stopPatch(this.stopSound(PandaGameRes.res.xiaoQuan));
            // root.audio.stopPatch(this.stopSound(PandaGameRes.res.daQuan));
            tween(this.innerRing).to(0.45, { angle: this.innerRing.angle - 5 }, { easing: easing.quadOut }).call(() => {
                this.innerWinEffGroup.children[this.mulWinIndex].active = true;
                this.outerWinEffGroup.children[this.outRingIndex].active = true;
                // AudioMgr.inst.playOneShot(PandaGameRes.res.guangQuanEff, "");
                this.innerWinEff.active = true;
                this.innerSpinEffNode.active = false;
                this.marble.active = false;
                this.ballTracking = false;

                if (this._spinEffId != -1) (AudioMgr.inst.stopOneShot(this._spinEffId, PandaGameRes.res.spinEff, ModuleDef.SLOTS_PANDA), this._spinEffId = -1);
            }).start();
            let uio2 = this.node.getComponent(UIOpacity);
            uio2.opacity = 255;
            tween(uio2).delay(3.45).to(0.2, { opacity: 0 }).call(() => {
                this.ballTracking = false;
                this.node.active = false;
                director.emit(GameEvent.exitMiniGame);
            }).start();
        }
    }
    stopSound(res: string) {
        return this.soundGroup.find(v => v.res == res)?.uuid;
    }
    onTouchOK() {
        console.log("点了OK");
        director.emit(PandaGameEvent.event.enterMiniGame, "");
        let uio = this.enterMiniGamePop.getComponent(UIOpacity);
        AudioMgr.inst.playOneShot(PandaGameRes.res.click, 1, ModuleDef.SLOTS_PANDA);

        this.marble.position = v3(0, 408.7);
        this.innerRing.angle = 0;
        this.outerRing.angle = 0;
        this.angle = 0;
        this._angle = 0;
        this.init();
        tween(uio).to(0.2, { opacity: 0 }).call(() => {
            // root.audio.stop(PandaGameRes.res.miniSpin);
            this.node.active = true;
            this.pandaAni.setAnimation(0, "animation", false);
            let uio2 = this.node.getComponent(UIOpacity);
            uio2.opacity = 0;
            tween(uio2).to(0.2, { opacity: 255 }).start();
            this.enterMiniGamePop.active = false;
        }).start();
    }
    playBallHit() {
        let audioArr = [PandaGameRes.res.ballHit1, PandaGameRes.res.ballHit2, PandaGameRes.res.ballHit3];
        audioArr.forEach((v, i) => {
            if (v.indexOf(this.nowPlayingSound) > -1) {
                audioArr.splice(i, 1);
            }
        })
        let sound = audioArr[Math.floor(Math.random() * audioArr.length)];
        this.nowPlayingSound = sound;
        AudioMgr.inst.playOneShot(this.nowPlayingSound, 1, ModuleDef.SLOTS_PANDA);
    }

    public isPointInCircleAround(point: Vec2, center: Vec2, radius: number, threshold: number = 0.92): boolean {
        // 计算点到圆心的距离
        const distance = Vec2.distance(point, center);
        // 判断距离是否小于等于半径加上阈值
        return distance > radius * threshold;
    }

    /**
     * 
     * @param vec1 
     * @param vec2 
     * @returns 两点夹角弧度
     */
    public getAngleBetweenVectors(vec1: Vec3, vec2: Vec3): number {
        // 计算向量的点积
        const dotProduct = vec1.x * vec2.x + vec1.y * vec2.y + vec1.z * vec2.z;
        // 计算向量的模长
        const magnitude1 = Math.sqrt(vec1.x * vec1.x + vec1.y * vec1.y + vec1.z * vec1.z);
        const magnitude2 = Math.sqrt(vec2.x * vec2.x + vec2.y * vec2.y + vec2.z * vec2.z);
        // 计算夹角的余弦值
        const cosineTheta = dotProduct / (magnitude1 * magnitude2);
        // 使用反余弦函数计算夹角的弧度值
        const angleInRadians = Math.acos(cosineTheta);
        return angleInRadians;
    }
}