import { sp } from 'cc';
import { Vec3 } from 'cc';
import { v2 } from 'cc';
import { ParticleSystem } from 'cc';
import { Label } from 'cc';
import { UITransform } from 'cc';
import { Sprite } from 'cc';
import { SpriteFrame } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { PandaGameData } from '../model/PandaGameData';
const { ccclass, property } = _decorator;

@ccclass('PandaWinIcon')
export class PandaWinIcon extends Component {
    //测试
    @property(SpriteFrame)
    iconSFGroup: SpriteFrame[] = [];

    //正式
    @property(sp.SkeletonData)
    iconSPGroup: sp.SkeletonData[] = [];
    @property(Label)
    priceTxt: Label;
    @property(ParticleSystem)
    wildEff: ParticleSystem;

    @property(ParticleSystem)
    wildTxtEff: ParticleSystem;
    private _isHit = false;
    public iconSP: sp.Skeleton;
    start() {
        this.iconSP = this.node.getComponent(sp.Skeleton);
        PandaGameData.setNodesByComponent(this.node);
    }
    //传入icon对应编号 测试-------------
    setSkin(number: number) {
        let index = 0;
        index = number < 200 ? number % 100 : 6;
        // console.log(index);
        //测试
        this.node.getComponent(Sprite).spriteFrame = this.iconSFGroup[index];

        //正式
        // this.node.getComponent(sp.Skeleton).skeletonData = this.iconSPGroup[index];
    }
    setPos(v3: Vec3) {
        this.node.position = v3;
    }
    //传入SpData对应编号 正式
    setSpData(number: number) {
        this.iconSP = this.node.getComponent(sp.Skeleton);
        let index = 0;
        index = number < 200 ? number % 100 - 1 : 6;
        if (index >= this.iconSPGroup.length - 1) {
            // console.log(index, this.iconSPGroup.length, "暂时没这个东西");
            this.iconSP.premultipliedAlpha = true;
        } else {
            // console.log(index);
            this.iconSP.premultipliedAlpha = false;
        }
        this.iconSP.skeletonData = this.iconSPGroup[index];
    }
    //正式
    playAni(isHit: boolean) {
        this._isHit = isHit;
        this.node.active = true;
        if (isHit) {
            let ani = this.iconSP.setAnimation(0, "2", false);
            let delay = this.iconSP.getState().tracks[0].animation.duration;
            // console.log("动画连线成功持续时间:", this.iconSP, ani, + delay);
            this.scheduleOnce(() => {
                this.iconSP.setAnimation(0, "3", false);
            }, delay);
        } else {
            this.iconSP.setAnimation(0, "3", false);
        }
    }
    playClickIcon() {
        this.playAni(true);
    }
    update(deltaTime: number) {

    }
}