import { sp } from 'cc';
import { _decorator, Component } from 'cc';
import { EPandaAni } from '../model/PandaInterface';
import { PandaGameMainView } from '../view/PandaGameMainView';
const { ccclass, property } = _decorator;

@ccclass('PandaAnimation')
export class PandaAnimation extends Component {
    public _ske: sp.Skeleton;
    public _callback: Function;
    aniSate = 0;
    protected start(): void {
        this._ske = this.getComponent(sp.Skeleton);
        this._ske.setCompleteListener(() => {
            if (this._callback) {
                this._callback();
                // console.log("动画完成");
                this._callback = null;
            }
        });
    }
    public play(type: EPandaAni, isLoop: boolean = false, callback?: Function) {
        this.aniSate = type;
        this._ske.paused = true;
        // console.log("播放的type：", type, isLoop, callback);
        this.scheduleOnce(() => {
            this._ske.skeletonData = PandaGameMainView.ins.anis[type];
            if (type != EPandaAni.Drowsy) {
                this._ske.node.active = true;
                this._ske.paused = false;
                this._ske.setAnimation(0, "animation", isLoop);
            }
        }, 0.005);
        callback && (this._callback = callback);
    }
    //瞌睡动画单独处理
    public playDrowsy(type: 1 | 2 | 3 | number, isLoop = false) {
        this._ske.node.active = true;
        this.scheduleOnce(() => {
            this._ske.paused = false;
            this._ske.setAnimation(0, type.toString(), isLoop);
        }, 0.005);
    }
    /**
     * 进入小游戏举起金币一系列动作
     * @param type 1:举起 2:待机 3:放回金币
     */
    public playMiniGold(type: 1 | 2 | 3, isLoop = false) {
        this._ske.node.active = true;
        this.scheduleOnce(() => {
            this._ske.paused = false;
            this._ske.setAnimation(0, type.toString(), isLoop);
        }, 0.005);
    }
}